.about-us{
    &__container{
        margin-bottom: 100px;
    }

    &__title{
        padding-bottom: 80px;
    }
}

.fact {
    width: 32%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;

   

    &__group-people-container{
        position: relative;
        width: 64px;
        height: 64px;
        margin-bottom: 40px;
    }

    &__img{
        width: 64px;
        height: 64px;
        margin-bottom: 40px;

        &_first {
            position: absolute;
            top: 1%;
            left: 10%;
            z-index: 2;
        }

        &_second {
            position: absolute;
            top: -7%;
            left: -38%;
            z-index: 1;
        }

        &_third {
            position: absolute;
            top: -7%;
            left: 60%;
            z-index: 1;
        }
    }

    &__group-img{
        width: 120px;
        height: 120px;
    }

    &__title {
        @include text(18px, 500, 130%);
        padding-bottom: 10px;
        text-align: center
    }

    &__text {
        @include text(14px, 300, 130%);
        text-align: center;
        max-height: 140px;
        overflow-y: auto;
    }
}